*{
  margin: 0px;
  padding: 0px;
}
body{
  margin: 0px;
  padding: 0px;
}

.cover_content{
   position: relative;
   top: 0;
}
.cov-content{
  position: absolute;
  top: 0;
  color: #fff;
  padding: 83px 0px 0 175px;
}
.cover_img{
  width: 100%;
  height: 500px;
}
.navbar-brand img{
  width: 100%;
  height: 30px;
}
a.navbar-brand {
  margin-right: 123px;
}
.cover{
  width: 100%;
}
.main-cover{
  position: relative;
}
.cover-content{
  position: absolute;
  top: 153px;
}
.footer{
  background: #000;
  color: #fff;
}
.about img{
   width: 100%;
}
.about{
  position: relative;
}
.about-content{
  position: absolute;
  top: 5px;
  color: #fff;
}

.skill{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.skill-box{
  width: 100%;
  margin: 25px 0;
}
.skill-box .title{
  display: block;
  font-size: 14px;
  font-weight: 800;
  color: #fff;
}
.skill-box .skill-bar{
  height: 8px;
  width: 100%;
  border-radius: 6px;
  margin-top: 6px;
  background:  rgba(0,0,0,0.1);
}

.skill-box .skill-per{
  position: relative;
  display: block;
  height: 100%;
  width: 95%;
  border-radius: 6px;
  background: #0062cc;
  animation: progress 0.4s ease-in-out forwards;
  opacity: 0;
}
.skill-per.css{
  width: 80%;
  animation-delay: 0.1s;
}

@keyframes progress{
  0%{
    width: 0;
    opacity: 1;
  }
  100%{
    opacity: 1;
  }
}

.skill-per .tooltip{
  position: absolute;
  right: -14px;
  top: -28px;
  font-size: 9px;
  font-weight: 500;
  color: #fff;
  padding: 2px 6px;
  border-radius: 3px;
  background: crimson;
  z-index: 1;
}



.skillimg{
  width: 100%;
}
.skills{
  position: relative;
}
.sket{
  position: absolute;
  top: 5px;
  color: #fff!important;
}
.skill1{
   width: 39%;
}
.skill2{
  width: 48%;
  background: url('../src/img/glass.png') no-repeat center center/cover;
  padding: 40px;
  border-radius: 128px 19px 20px 20px;
}
.project img {
  width: 100%;
  height: 256px;
}
.project{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.project div{
  width: 30%;
}
.contacte img {
  width: 100%;
  position: relative;
  top: 0;
}
.form-contact {
  position: absolute;
  top: 130px;
  background: url('http://localhost:3000/static/media/glass.58f5fc267284345a8864.png') no-repeat center center/cover;
  padding: 40px;
  left: 100px;
  border-radius: 21px;
  width: 45%;
}
